body {
  font-family: Arial, sans-serif;
  background-color: #faf8ef;
  color: #776e65;
  margin: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
}

.game-container {
  text-align: center;
}

h1 {
  font-size: 48px;
  margin-bottom: 10px;
}

.game-info {
  margin-bottom: 20px;
}

.restart-button {
  background-color: #8f7a66;
  color: white;
  border: none;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  border-radius: 4px;
}

.restart-button:hover {
  background-color: #a07f68;
}

.game-board {
  display: grid;
  grid-template-columns: repeat(4, 80px);
  gap: 10px;
  background-color: #bbada0;
  padding: 10px;
  border-radius: 6px;
}

.field-cell {
  width: 80px;
  height: 80px;
  background-color: #cdc1b4;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 24px;
  font-weight: bold;
  border-radius: 4px;
}

.field-cell--2 {
  background-color: #eee4da;
  color: #776e65;
}

.field-cell--4 {
  background-color: #ede0c8;
  color: #776e65;
}

.field-cell--8 {
  background-color: #f2b179;
  color: white;
}

.field-cell--16 {
  background-color: #f59563;
  color: white;
}

.field-cell--32 {
  background-color: #f67c5f;
  color: white;
}

.field-cell--64 {
  background-color: #f65e3b;
  color: white;
}

.field-cell--128 {
  background-color: #edcf72;
  color: white;
}

.field-cell--256 {
  background-color: #edcc61;
  color: white;
}

.field-cell--512 {
  background-color: #edc850;
  color: white;
}

.field-cell--1024 {
  background-color: #edc53f;
  color: white;
}

.field-cell--2048 {
  background-color: #edc22e;
  color: white;
}

.game-over {
  font-size: 32px;
  font-weight: bold;
  margin-top: 20px;
  color: #e74c3c;
  display: none;
}

.win {
  font-size: 32px;
  font-weight: bold;
  margin-top: 20px;
  color: #27ae60;
  display: none;
}

.hidden {
  display: none !important; // Ensure it overrides other styles
}

.message-lose {
  display: none;
  color: #e74c3c;
  font-size: 24px;
  font-weight: bold;
  text-align: center;
}

.message-lose.hidden {
  display: block;
}
