  background-color: #8f7a66;
  border: none;
  border-radius: 4px;
  padding: 10px 20px;
  font-size: 16px;
}

.restart-button:hover {
  background-color: #a07f68;
}

.game-board {
  background-color: #bbada0;
  border-radius: 6px;
  grid-template-columns: repeat(4, 80px);
  gap: 10px;
  padding: 10px;
  display: grid;
}

.field-cell {
  background-color: #cdc1b4;
  border-radius: 4px;
